import { getAttributes, getChildren, hasAttributes, JsonML } from '@ali/4ever-utils';
import { MoState as State, createTextWithString } from '@ali/4ever-cangjie';

const isBlockquote = (node: JsonML | null): boolean => {
  if (node && hasAttributes(node) && node[1]['blockquote']) {
    return true;
  }
  return false;
};

export default {
  name: 'blockquote',

  onOpenTag: (state: State, name: string): boolean => {
    if (name !== 'blockquote') return false;


    const blockquote: JsonML = [
      'p',
      {
        blockquote: true,
      },
      createTextWithString(''),
    ];
    state.push(blockquote);
    return true;
  },

  onCloseTag: (state: State, name: string): boolean => {
    if (name !== 'blockquote' && !/^(h[1-6]|p)$/.test(name)) return false;

    const parent = state.getParent();
    const current = state.peek();
    // 处理 blockquote + p/h1-6 嵌套的情况(长期来看要修改 blockquote 的实现，才能较好地支持引用内潜逃其他元素的情况)
    // 与 blockquote 节点合并
    if (name !== 'blockquote' && isBlockquote(parent)) {
      state.pop();
      const node: JsonML = [
        current![0],
        {
          blockquote: true,
          ...(getAttributes<object>(current) || {})
        },
        ...(getChildren(parent!) || []),
        ...(getChildren(current) || []),
      ];
      state.pop();
      state.push(node);
      return true;
    }
    if (name === 'blockquote' && !state.isStackEmpty()) {
      const blockquote = state.pop();
      state.append(blockquote);
      return true;
    }
    return false;
  },
};
